import { mapState, mapMutations } from 'vuex';
export default {
	computed:{
		...mapState(["code"])
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'健康生活'
		})
	},
	onLoad(){
		uni.$on('Wechat_login', this.login)
		uni.$on('Wechat_GetOpenId', this.getOpenId)
		uni.$on('Wechat_CheckUser', this.checkUser)
		uni.$on('Wechat_RegisterUser', this.registerUser)
	},
	data:function(){
		return {
			currentUser:null
		}
	},
	methods:{
		...mapMutations(["setCode", "setCurrentUser"]),
		onGetUserInfo(res){
			if(res.detail && res.detail.userInfo){
				this.$store.commit("setCurrentUser", res.detail.userInfo)
				this.currentUser = res.detail.userInfo;
				uni.$emit('Wechat_login')
			}
		},
		login(){
			uni.login({
				provider:"weixin",
				success:res=>{
					this.currentUser.code = res.code;
					uni.$emit('Wechat_GetOpenId')
				},
			});
		},
		getOpenId(){
			wx.request({
				url:'http://qingyun.kiwihealthcare123.com/getopenid.py?code=' + this.currentUser.code,
				method:'GET',
				success:res=>{
					if(res.statusCode == 200){
						let data = JSON.parse(res.data.replace(/\'/g, '\"'))
						uni.$emit('Wechat_CheckUser', {openid:data.openid})
					}
				},
				fail:res=>{
					console.log(res)
				},
			});
		},
		checkUser(openid){
			this.currentUser.OpenId = openid.openid;
			wx.request({
				url:'http://39.98.107.68:8000/api/endusers/'+this.currentUser.OpenId,
				method:"GET",
				dataType:'json',
				success:res=>{
					if(res.data && res.data !== ""){
						this.currentUser = res.data;
						this.$store.commit('setCurrentUser', this.currentUser)
						uni.switchTab({
							url:'../index/index'
						})
					} else {
						uni.$emit('Wechat_RegisterUser')
					}
				},
				fail:res=>{
					console.log(res)
				},
				complete:()=>{
					console.log("complete")
				}
			})
		},
		registerUser(){
			wx.request({
				url:'http://39.98.107.68:8000/api/endusers/',
				dataType:'json',
				method:'POST',
				data:{
					"OpenId":this.currentUser.OpenId,
					"Name":this.currentUser.nickName,
					"PSW":"123",
					"PhoneNumber":"123456"
				},
				success:res=>{
					this.currentUser = res.data;
					this.$store.commit('setCurrentUser', this.currentUser)
					uni.switchTab({
						url:'../index/index'
					})
				},
				fail:res=>{
					console.log(res)
				}
			})
		},
		getPhoneNumber(res){
			console.log(res)
		},
		wechat_login(){
			let _this = this;
			
			uni.login({
				provider:"weixin",
				success:res=>{
					console.log(res);
					// if(res.errMsg == "login:ok"){
					// 	_this.$state.commit("setCode", res.code);
					// }
					uni.getUserInfo({
						provider:"weixin",
						success:res=>{
							console.log(res);
							// if(res.errMsg == "login:ok"){
							// 	_this.$state.commit("setCode", res.code);
							// }
						},
						fail:res=>{
							console.log(res);
						}
					})
				},
				fail:res=>{
					console.log(res);
				}
			})
		},
		getUserInfo(e){
			console.log(e)
		},
		phone_login(){
			
			uni.navigateTo({
				url:"./phoneLogin"
			})
		},
		goToClause(){
			
			uni.navigateTo({
				url:"./clause"
			})
		}
	}
}
